import React, { useState, useEffect } from 'react';
import '../styles/theme.css';
import './TrumpetTipsPage.css';
import TimeStamp from '../components/TimeStamp';
import PanelWrapper from '../components/ui/PanelWrapper';
import Card from '../components/ui/card';
import Button from '../components/ui/button';

interface TrumpetTipsPageProps {}

// Interface for the breathing exercise timer
interface BreathingExerciseState {
  isRunning: boolean;
  phase: 'inhale' | 'hold' | 'exhale' | 'rest';
  timeLeft: number;
  counts: {
    inhale: number;
    hold: number;
    exhale: number;
    rest: number;
  };
}

// Valve patterns for common trumpet notes (simplified for beginners)
const BEGINNER_TRUMPET_FINGERINGS = {
  'C4': [0, 0, 0], // Open (no valves)
  'D4': [1, 3, 0], // Valves 1 and 3
  'E4': [1, 2, 0], // Valves 1 and 2
  'F4': [1, 0, 0], // Valve 1
  'G4': [0, 0, 0], // Open (no valves)
  'A4': [1, 2, 0], // Valves 1 and 2
  'Bb4': [1, 0, 0], // Valve 1
  'C5': [0, 0, 0], // Open (no valves)
};

const TrumpetTipsPage: React.FC<TrumpetTipsPageProps> = () => {
  // State for collapsible sections
  const [expandedSections, setExpandedSections] = useState<{[key: string]: boolean}>({
    'getting-started': true,
    'embouchure': true,
    'breathing': true,
    'practice': true,
    'problems': true,
    'resources': true
  });

  // State for the breathing exercise timer
  const [breathingExercise, setBreathingExercise] = useState<BreathingExerciseState>({
    isRunning: false,
    phase: 'inhale',
    timeLeft: 4,
    counts: {
      inhale: 4,
      hold: 4,
      exhale: 4,
      rest: 2
    }
  });

  // State for selected fingering note
  const [selectedNote, setSelectedNote] = useState<string | null>('C4');

  // Timer effect for breathing exercise
  useEffect(() => {
    let timer: NodeJS.Timeout | null = null;
    
    if (breathingExercise.isRunning) {
      timer = setInterval(() => {
        setBreathingExercise(prev => {
          const newTimeLeft = prev.timeLeft - 1;
          
          if (newTimeLeft <= 0) {
            // Move to next phase
            let nextPhase: 'inhale' | 'hold' | 'exhale' | 'rest';
            let nextTimeLeft: number;
            
            switch (prev.phase) {
              case 'inhale':
                nextPhase = 'hold';
                nextTimeLeft = prev.counts.hold;
                break;
              case 'hold':
                nextPhase = 'exhale';
                nextTimeLeft = prev.counts.exhale;
                break;
              case 'exhale':
                nextPhase = 'rest';
                nextTimeLeft = prev.counts.rest;
                break;
              case 'rest':
                nextPhase = 'inhale';
                nextTimeLeft = prev.counts.inhale;
                break;
              default:
                nextPhase = 'inhale';
                nextTimeLeft = prev.counts.inhale;
            }
            
            return {
              ...prev,
              phase: nextPhase,
              timeLeft: nextTimeLeft
            };
          } else {
            return {
              ...prev,
              timeLeft: newTimeLeft
            };
          }
        });
      }, 1000);
    }
    
    return () => {
      if (timer) clearInterval(timer);
    };
  }, [breathingExercise.isRunning]);
  
  // Toggle section expansion
  const toggleSection = (section: string) => {
    setExpandedSections(prev => ({
      ...prev,
      [section]: !prev[section]
    }));
  };
  
  // Start/stop breathing exercise
  const toggleBreathingExercise = () => {
    setBreathingExercise(prev => ({
      ...prev,
      isRunning: !prev.isRunning,
      timeLeft: prev.isRunning ? prev.timeLeft : prev.counts.inhale,
      phase: prev.isRunning ? prev.phase : 'inhale'
    }));
  };
  
  // Update breathing exercise counts
  const updateBreathingCounts = (phase: 'inhale' | 'hold' | 'exhale' | 'rest', value: number) => {
    setBreathingExercise(prev => ({
      ...prev,
      counts: {
        ...prev.counts,
        [phase]: value
      },
      timeLeft: prev.phase === phase ? value : prev.timeLeft
    }));
  };
  
  // Render trumpet valves with fingering
  const renderTrumpetFingering = (fingering: number[]) => {
    return (
      <div className="trumpet-interactive-fingering">
        <div className="trumpet-valves">
          {fingering.map((pressed, index) => (
            <div key={index} className={`trumpet-valve ${pressed ? 'pressed' : ''}`}>
              <div className="trumpet-valve-cap"></div>
              <div className="trumpet-valve-stem"></div>
              <span className="valve-number">{index + 1}</span>
            </div>
          ))}
        </div>
        <div className="trumpet-body">
          <div className="trumpet-mouthpiece"></div>
          <div className="trumpet-main-tube"></div>
          <div className="trumpet-bell"></div>
        </div>
      </div>
    );
  };
  
  // Render embouchure diagram
  const renderEmbouchureDiagram = () => {
    return (
      <div className="embouchure-diagram">
        <div className="embouchure-examples">
          <div className="embouchure-example correct">
            <div className="embouchure-face">
              <div className="embouchure-lips correct">
                <div className="upper-lip"></div>
                <div className="lower-lip"></div>
              </div>
              <div className="embouchure-mouthpiece"></div>
            </div>
            <p className="embouchure-label">Correct Embouchure</p>
            <ul className="embouchure-points">
              <li>Corners firm</li>
              <li>Center relaxed</li>
              <li>50/50 placement</li>
            </ul>
          </div>
          
          <div className="embouchure-example incorrect">
            <div className="embouchure-face">
              <div className="embouchure-lips incorrect">
                <div className="upper-lip puffed"></div>
                <div className="lower-lip puffed"></div>
              </div>
              <div className="embouchure-mouthpiece"></div>
            </div>
            <p className="embouchure-label">Incorrect Embouchure</p>
            <ul className="embouchure-points">
              <li>Puffed cheeks</li>
              <li>Loose corners</li>
              <li>Uneven pressure</li>
            </ul>
          </div>
        </div>
      </div>
    );
  };
  
  return (
    <PanelWrapper className="tips-wrapper">
      <h2 className="page-title">Trumpet Tips for Beginners</h2>
      
      <div className="tips-container">
        <Card className="tip-section">
          <h3 
            onClick={() => toggleSection('getting-started')} 
            className="section-header"
          >
            Getting Started {expandedSections['getting-started'] ? '' : ''}
          </h3>
          {expandedSections['getting-started'] && (
            <div className="section-content">
              <div className="tip-card">
                <h4>Proper Assembly and Handling</h4>
                <p>
                  Always handle your trumpet with clean hands and avoid touching the valves unnecessarily.
                  When assembling, make sure all slides are properly seated and valves are properly aligned.
                  Never force any parts together.
                </p>
              </div>
              
              <div className="tip-card">
                <h4>Daily Maintenance</h4>
                <p>
                  After playing, empty all water from the trumpet using the water keys.
                  Wipe down the exterior with a clean, soft cloth to remove fingerprints and oils.
                  Store your trumpet in its case when not in use to protect it from dust and damage.
                </p>
              </div>
              
              <div className="tip-card">
                <h4>Weekly Maintenance</h4>
                <p>
                  Once a week, remove and clean your mouthpiece with mild soap and lukewarm water.
                  Apply a small amount of valve oil to each valve by removing them one at a time.
                  Apply slide grease to the tuning slide to keep it moving freely.
                </p>
              </div>
            </div>
          )}
        </Card>
        
        <Card className="tip-section">
          <h3 
            onClick={() => toggleSection('embouchure')} 
            className="section-header"
          >
            Developing Your Embouchure {expandedSections['embouchure'] ? '' : ''}
          </h3>
          {expandedSections['embouchure'] && (
            <div className="section-content">
              <div className="tip-card">
                <h4>What is Embouchure?</h4>
                <p>
                  Embouchure refers to the way you position your lips, facial muscles, and teeth to play the trumpet.
                  A proper embouchure creates a controllable vibration that produces a clear tone.
                </p>
              </div>
              
              {renderEmbouchureDiagram()}
              
              <div className="tip-card">
                <h4>Developing Your Embouchure</h4>
                <ul>
                  <li><strong>Start with a buzzing exercise:</strong> Practice making a buzzing sound with just your lips, then with the mouthpiece alone.</li>
                  <li><strong>Keep corners firm:</strong> The corners of your mouth should be firm, while the center remains flexible for vibration.</li>
                  <li><strong>Avoid excessive pressure:</strong> Don't press the mouthpiece too hard against your lips. Find a comfortable balance.</li>
                  <li><strong>Practice daily:</strong> Spend 5-10 minutes each day on embouchure exercises to build strength and control.</li>
                </ul>
              </div>
            </div>
          )}
        </Card>
        
        <Card className="tip-section">
          <h3 
            onClick={() => toggleSection('breathing')} 
            className="section-header"
          >
            Breathing and Air Support {expandedSections['breathing'] ? '' : ''}
          </h3>
          {expandedSections['breathing'] && (
            <div className="section-content">
              <div className="tip-card">
                <h4>Proper Breathing Technique</h4>
                <p>
                  Take a deep breath that fills your lungs from the bottom up. Your stomach should expand first, followed by your chest.
                  Keep your shoulders relaxed and avoid raising them when inhaling.
                  Focus on taking in as much air as possible, and then using it efficiently.
                </p>
              </div>
              
              <div className="tip-card">
                <h4>Air Support Exercises</h4>
                <ul>
                  <li><strong>Long tones:</strong> Practice holding notes for as long as possible with a steady, consistent sound.</li>
                  <li><strong>Breathing gym:</strong> Practice inhaling for 4 counts, holding for 4, and exhaling for 4 to develop lung capacity.</li>
                  <li><strong>Paper against the wall:</strong> Try to hold a piece of paper against the wall using only your breath.</li>
                </ul>
              </div>
              
              <div className="interactive-card breathing-exercise">
                <h4>Interactive Breathing Exercise Timer</h4>
                <div className="breathing-phase">
                  {breathingExercise.phase.toUpperCase()}: {breathingExercise.timeLeft}s
                </div>
                <div className="breathing-progress">
                  <div 
                    className={`progress-bar ${breathingExercise.phase}`}
                    style={{ 
                      width: `${(breathingExercise.timeLeft / breathingExercise.counts[breathingExercise.phase]) * 100}%` 
                    }}
                  ></div>
                </div>
                
                <Button 
                  variant={breathingExercise.isRunning ? 'danger' : 'primary'}
                  emoji={breathingExercise.isRunning ? '' : ''}
                  onClick={toggleBreathingExercise}
                  className="breathing-toggle"
                >
                  {breathingExercise.isRunning ? 'Stop Exercise' : 'Start Exercise'}
                </Button>
                
                <div className="breathing-settings">
                  <div className="setting">
                    <label>Inhale: {breathingExercise.counts.inhale}s</label>
                    <input 
                      type="range" 
                      min="2" 
                      max="8" 
                      value={breathingExercise.counts.inhale}
                      onChange={e => updateBreathingCounts('inhale', parseInt(e.target.value))}
                    />
                  </div>
                  <div className="setting">
                    <label>Hold: {breathingExercise.counts.hold}s</label>
                    <input 
                      type="range" 
                      min="2" 
                      max="8" 
                      value={breathingExercise.counts.hold}
                      onChange={e => updateBreathingCounts('hold', parseInt(e.target.value))}
                    />
                  </div>
                  <div className="setting">
                    <label>Exhale: {breathingExercise.counts.exhale}s</label>
                    <input 
                      type="range" 
                      min="2" 
                      max="8" 
                      value={breathingExercise.counts.exhale}
                      onChange={e => updateBreathingCounts('exhale', parseInt(e.target.value))}
                    />
                  </div>
                  <div className="setting">
                    <label>Rest: {breathingExercise.counts.rest}s</label>
                    <input 
                      type="range" 
                      min="1" 
                      max="5" 
                      value={breathingExercise.counts.rest}
                      onChange={e => updateBreathingCounts('rest', parseInt(e.target.value))}
                    />
                  </div>
                </div>
              </div>
              
              <div className="tip-card">
                <h4>The Importance of Saying "TOO"</h4>
                <p>
                  When starting a note, think about saying "too" rather than "ta" or "the."
                  This helps create a focused air stream and a cleaner attack on the note.
                  Practice articulating with a clear "t" sound followed by a continuous "oo" sound.
                </p>
              </div>
            </div>
          )}
        </Card>
        
        <Card className="tip-section">
          <h3 
            onClick={() => toggleSection('practice')} 
            className="section-header"
          >
            Practice Strategies {expandedSections['practice'] ? '' : ''}
          </h3>
          {expandedSections['practice'] && (
            <div className="section-content">
              <div className="tip-card">
                <h4>Daily Practice Routine</h4>
                <ul>
                  <li><strong>Warm-up (5-10 min):</strong> Long tones, lip slurs, and simple scales to warm up your embouchure.</li>
                  <li><strong>Technique (10-15 min):</strong> Scales, arpeggios, and technical exercises to build finger dexterity.</li>
                  <li><strong>Repertoire (10-15 min):</strong> Work on pieces or etudes that challenge your current skill level.</li>
                  <li><strong>Cool-down (5 min):</strong> Easy, low register notes to relax your embouchure.</li>
                </ul>
              </div>
              
              <div className="tip-card">
                <h4>Effective Practice Tips</h4>
                <ul>
                  <li>Practice with a metronome to develop a steady sense of time.</li>
                  <li>Start slowly and gradually increase speed as you master a passage.</li>
                  <li>Record yourself to identify areas that need improvement.</li>
                  <li>Practice difficult passages in small chunks before putting them together.</li>
                  <li>End each practice session with something you enjoy playing.</li>
                </ul>
              </div>
              
              <div className="tip-card">
                <h4>Using a Practice Journal</h4>
                <p>
                  Keep a practice journal to track your progress and set goals. Record what you practiced, for how long, at what tempos, and any challenges you encountered. This helps you stay organized and motivated as you see your improvement over time.
                </p>
              </div>
            </div>
          )}
        </Card>
        
        <Card className="tip-section">
          <h3 
            onClick={() => toggleSection('problems')} 
            className="section-header"
          >
            Troubleshooting Common Problems {expandedSections['problems'] ? '' : ''}
          </h3>
          {expandedSections['problems'] && (
            <div className="section-content">
              <div className="tip-card">
                <h4>Weak High Register</h4>
                <p>
                  If you struggle with high notes, focus on strengthening your embouchure with lip slurs and long tones. Practice playing in the middle register with a full, supported sound, then gradually work your way up. Avoid using excessive pressure against your lips.
                </p>
              </div>
              
              <div className="tip-card">
                <h4>Inconsistent Tone Quality</h4>
                <p>
                  Inconsistent tone often comes from irregularities in your air support or embouchure. Practice long tones while focusing on maintaining a steady air stream and consistent embouchure. Record yourself to identify when your tone changes.
                </p>
              </div>
              
              <div className="tip-card">
                <h4>Endurance Issues</h4>
                <p>
                  Build endurance gradually by extending your practice sessions over time. Take breaks when your embouchure feels tired, and never practice through pain. Focus on efficiency in your playingusing just the right amount of effort, not more.
                </p>
              </div>
              
              <div className="tip-card">
                <h4>Difficulty Articulating</h4>
                <p>
                  Practice articulation by starting with simple patterns (like quarter notes) while saying "too" as you play. Gradually increase the speed and complexity. Ensure your tongue is striking the right placejust behind the upper teeth, not between the lips.
                </p>
              </div>
            </div>
          )}
        </Card>
        
        <Card className="tip-section">
          <h3 
            onClick={() => toggleSection('resources')} 
            className="section-header"
          >
            Additional Resources {expandedSections['resources'] ? '' : ''}
          </h3>
          {expandedSections['resources'] && (
            <div className="section-content">
              <div className="tip-card">
                <h4>Recommended Method Books</h4>
                <ul>
                  <li><strong>Arban's Complete Conservatory Method:</strong> The "trumpet bible" with exercises for all levels</li>
                  <li><strong>Clarke's Technical Studies:</strong> Excellent for building finger technique</li>
                  <li><strong>Stamp's Warm-Ups + Studies:</strong> Great for embouchure development</li>
                  <li><strong>Rubank Elementary/Intermediate Method:</strong> Structured progression for beginners</li>
                </ul>
              </div>
              
              <div className="tip-card">
                <h4>Online Learning Resources</h4>
                <ul>
                  <li><strong>YouTube tutorials:</strong> Many professional trumpeters offer free lessons</li>
                  <li><strong>Trumpet forums:</strong> Connect with other players for advice and motivation</li>
                  <li><strong>Trumpet apps:</strong> Use apps for tuning, metronome, and practice tracking</li>
                  <li><strong>Virtual sheet music libraries:</strong> Access a wide range of music to practice</li>
                </ul>
              </div>
              
              <div className="tip-card">
                <h4>Finding a Teacher</h4>
                <p>
                  While self-teaching is possible, working with a qualified teacher can accelerate your progress and help you avoid developing bad habits. Look for teachers at local music schools, universities, or through online platforms that offer virtual lessons.
                </p>
                <div style={{ marginTop: '15px', textAlign: 'center' }}>
                  <Button 
                    variant="primary"
                    emoji=""
                    onClick={() => window.open('https://www.google.com/search?q=trumpet+teachers+near+me', '_blank')}
                  >
                    Find Teachers Near Me
                  </Button>
                </div>
              </div>
            </div>
          )}
        </Card>
      </div>
      
      <div style={{ textAlign: 'center', marginTop: '30px' }}>
        <Button 
          variant="secondary"
          emoji=""
          onClick={() => window.history.back()}
        >
          Back to Menu
        </Button>
      </div>
      
      <TimeStamp />
    </PanelWrapper>
  );
};

export default TrumpetTipsPage; 